export const DATE_TEMPLATE = (date) =>
  `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(
    date.getDate(),
  ).padStart(2, '0')}`;

export const DATE_INTERVAL = [
  { index: 0, interval: 0, display: '' },
  { index: 1, interval: 7, display: '7' },
  { index: 2, interval: 15, display: '15' },
  { index: 3, interval: 30, display: '1' },
  { index: 4, interval: 91, display: '3' },
  { index: 5, interval: 365, display: '1' },
];
