import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { colors } from '../../../styles/color';
import { DATE_TEMPLATE } from '../../../utils/dateSelector';

function QuestionTable({ questions }) {
  return (
    <StyledGridWrapper>
      <GridHeader></GridHeader>
      <GridHeader></GridHeader>
      <GridHeader> </GridHeader>
      {questions.length !== 0 ? (
        questions.map((question) => (
          <>
            <GridBody>{DATE_TEMPLATE(new Date(question.date))}</GridBody>
            <GridBody>{question.title}</GridBody>
            <GridBody>{question.status ? '' : ''}</GridBody>
          </>
        ))
      ) : (
        <div>     .</div>
      )}
    </StyledGridWrapper>
  );
}

QuestionTable.propTypes = {
  questions: PropTypes.array,
};

const GridHeader = styled.div`
  padding: 18px 0;
  color: ${colors.gray5};
  border-bottom: 1px solid ${colors.gray3};
  border-top: 2px solid ${colors.gray8};
  &:nth-child(3n - 1) {
    text-align: left;
  }
`;

const GridBody = styled.div`
  padding: 42px 0;
  color: ${colors.gray7};
  border-bottom: 1px solid ${colors.gray3};
  &:nth-child(3n - 1) {
    text-align: left;
  }
`;

const StyledGridWrapper = styled.div`
  display: grid;
  grid-template-columns: 120px auto 120px;
  text-align: center;
  font-size: 13px;
`;

export default QuestionTable;
